html {
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
}

.App {
  width: 400px;
  margin: 50px auto;
  &__header {
    display: flex;
    align-items: center;
    gap: 20px;
  }
}

.Good {
  display: flex;
  gap: 10px;

  &--active {
    background-color: #ff0;
  }
}

.list {

  &__item {
    display: flex;
    justify-content: space-between;
    align-self: center;
  }
}

.button {
  align-content: center;
}
